import { contextBridge } from 'electron';
import fs from 'fs';
//import downloadBg2Scene from './scene-download.mjs';

contextBridge.exposeInMainWorld('fsAPI', {
    async writeFile(path, content, options) {
        return await fs.promises.writeFile(path, content, options);
    },

    async downloadScene(sceneFileUrl, dstPath) {
  //      return await downloadBg2Scene(sceneFileUrl, dstPath);
    },

    getDependencyVersion(name) {
        return process.versions[name];
    }
});
